export default {
	data() {
		return {
			currentIndex: 0,
			navTitles: ['未处理', '已处理'],
			handledList: [],
			unhandledList: [],
			hackReset: true
		}
	},

	onLoad() {
		this.fetchHandledList()
		this.fetchUnhandledList()
	},

	methods: {
		switchTab(index) {
			this.currentIndex = index
		},

		// 获取已处理列表
		fetchHandledList() {
			uni.request({
				url: 'https://ciai.le-cx.com/index.php/api/Alarm/recordList',
				data: {
					status: 1
				},
				success: res => {
					this.handledList = res.data.data
					// console.log(this.handledList)
				}
			})
		},

		// 获取未处理列表
		fetchUnhandledList() {
			uni.request({
				url: 'https://ciai.le-cx.com/index.php/api/Alarm/recordList',
				data: {
					status: -1
				},
				success: res => {
					this.unhandledList = res.data.data
					// console.log(this.unhandledList)
				}
			})
		},

		// 立即处理报警
		handWaring(id) {
			uni.request({
				url: `https://ciai.le-cx.com/index.php/api/Alarm/handleAlarm?id=${id}`,
				success: res => {
					uni.showToast({
						icon: 'success',
						title: res.data.msg,
						duration: 2000
					})
					// 重新获取数据
					this.fetchHandledList()
					this.fetchUnhandledList()
				},
			})
		},

		// 时间戳转时间格式
		time_format(timestamp) {
			//时间戳为10位需*1000，时间戳为13位的话不需乘1000
			let date = new Date(timestamp * 1000);
			let h = date.getHours();
			h = h < 10 ? ('0' + h) : h; //小时补0
			let m = date.getMinutes();
			m = m < 10 ? ('0' + m) : m; //分钟补0
			let s = date.getSeconds();
			s = s < 10 ? ('0' + s) : s; //秒补0
			return h + ':' + m + ':' + s;
		},
	}
}
